<template>
  <div id="app">
    <cv-header aria-label="Carbon header">
  <cv-header-menu-button aria-label="Header menu" aria-controls="side-nav" />
  <cv-skip-to-content href="#main-content">
    Skip to content
  </cv-skip-to-content>
  <cv-header-name href="javascript:void(0)">
    Krypton <span class="version" v-if="version">v{{version}}</span>
  </cv-header-name>
  <cv-header-nav aria-label="Carbon nav">
</cv-header-nav>
</cv-header>
    <div class="sample">
      <cv-data-table title="Deployed Models" v-if="!isLoading && models"
  :columns="columns" :data="models" ref="table"></cv-data-table>
      <cv-data-table-skeleton
        v-if="isLoading"
        :columns="columns"
        :rows="skeletonRows"
        title="Deployed Models"
      ></cv-data-table-skeleton>
    </div>
  </div>
</template>

<script>
import Axios from 'axios';

export default {
  name: 'App',
  data() {
    return {
      isLoading: true,
      version: null,
      models: [],
      columns: [
        'Model',
        'Endpoint',
        'Status',
      ],
      skeletonRows: 5,
    };
  },
  created() {
    this.loadVersion();
    this.loadModels();
  },
  methods: {
    loadModels() {
      this.isLoading = true;
      Axios.get('/api/v1/models').then((response) => {
        if (response && response.data && response.data.data) {
          response.data.data.forEach((element) => {
            this.models.push([element.model, element.endpoint, element.status]);
          });
        }
        this.isLoading = false;
      }).catch((err) => {
        console.log(err);
        this.isLoading = false;
      });
    },
    loadVersion() {
      this.isLoading = true;
      Axios.get('/api/v1/health').then((response) => {
        if (response && response.data && response.data.version) {
          this.version = response.data.version;
        }
        this.isLoading = false;
      }).catch((err) => {
        console.log(err);
        this.isLoading = false;
      });
    },
  },
};
</script>

